<template>
    <div id="countdown">
        <div 
            v-if="this.days <=0 && this.hours <= 0 && this.minutes <= 0 && this.seconds <=0"
        >
            <h1 class="countdown_title text-center">Que la meilleure promo gagne !</h1>
            <Classement/>   
        </div>
        <div 
            v-else
        >
            <h1 class="countdown_title text-center">Clash des titans dans </h1>
            <div id="countdown_container">
                <span class="time-container">
                    <div class="time"> {{ days }} </div>
                    <div class="indicator">Jours</div>
                </span>
                <span class="time-container">
                    <div class="time"> {{ hours }} </div>
                    <div class="indicator">Heures</div>
                </span>
                <span class="time-container">
                    <div class="time"> {{ minutes }} </div>
                    <div class="indicator">Minutes</div>
                </span>
                <span class="time-container">
                    <div class="time"> {{ seconds }} </div>
                    <div class="indicator">Secondes</div>
                </span>
            </div>
        </div>
    </div>
</template>
<script>

import Classement from './Classement'

export default {
    name : "Countdown",
    components: {
        Classement
    },
    data(){
        return {
            years : 0,
            days : 0,
            hours : 0,
            minutes : 0,
            seconds : 0,
            previousDiff: {}
        }
    },
    methods: {
        refreshCountdown(){
            const MINUTES = 60
            const HOURS = 60 * MINUTES
            const DAYS = 24 * HOURS
            
            var now = Math.floor(Date.now() / 1000)
            // var event = Date.parse('2020-03-11T00:00:00+0000') /1000
            var event = Date.parse('2021-03-25T14:00:00+0000') /1000
            
            
            var diff = event - now - HOURS

            var left = {
                days :Math.floor(diff / DAYS),
                hours:Math.floor(diff % DAYS / HOURS),
                minutes:Math.floor(diff % HOURS / MINUTES),
                seconds:Math.floor(diff % MINUTES)
            }

           
            Object.keys(left).forEach((key) => {
                if(this.previousDiff[key] !== left[key]) {
                    this[key] = left[key]
                }
            }) 
            this.previousDiff = left
             window.setTimeout(()=> {
                if(!(this.days <=0 && this.hours <= 0 && this.minutes <= 0 && this.seconds <=0)){

                    window.requestAnimationFrame(this.refreshCountdown)
                }
            } , 1000)
        }

    
    },
    mounted(){
       this.refreshCountdown()
    }
}
</script>
